
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Alert from 'vux/src/components/alert/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';
export default {
directives: {
TransferDom
},
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
DatetimePlugin,
Datetime,
Group,
Picker,
Alert,
Previewer
},
data() {
return {
value1: '',
startTime:"",
endTime:'',
hourListValue:'',
hot:'',
ListValue:'',
activeContent:'',
listSingle:{},
userName:'',
picList:{list:[],arr:[]},
};
},
methods: {
showDeleteButton(it) {
clearInterval(this.Loop);
var This = this;
this.Loop=setTimeout(function(){
This.picList.list.splice(it,1);
This.picList.arr.splice(it,1);
This.$vux.alert.show({title:'删除成功'});
},1000);
},
clearLoop() {
clearInterval(this.Loop);
},
atBig (item) {
},
getList(){
axios.get('pscoredetail/queryById',{
params: {
id:this.$route.params.moduleId
}
}).then(res =>{
this.listSingle=res.data
}).catch(err =>{
console.log('fail'+err.data)

})
},
getUser() {
axios.get('ppartymember/queryByUserId', {
params: {
userid: this.$store.getters.user.userid
}
})
.then(res => {
this.userName = res.data.name;
})
.catch(err => {
console.log(err);
});
},
openPicker() {
this.$refs.picker.open();
},
submit(){
var starttime = this.startTime.replace(new RegExp("-","gm"),"/");
var starttimeHaoMiao = (new Date(starttime)).getTime();
var endtime = this.endTime.replace(new RegExp("-","gm"),"/");
var endtimeHaoMiao = (new Date(endtime)).getTime();

if(starttimeHaoMiao<endtimeHaoMiao){


axios({
url:'pstudy/save',
method:'post',
headers: {'contentType':'application/json'},
params:{
departmentid:this.$store.getters.user.departmentid,
createUserid:this.$store.getters.user.userid,
roleid:this.$store.getters.user.roleid,
starttime:new Date(this.startTime.replace(/-/gi,'/')).getTime(),
endtime:new Date(this.endTime.replace(/-/gi,'/')).getTime(),
projectid:this.$route.params.projectId,
moduleid:this.$route.params.moduleId,
content:this.activeContent,
picids:this.picList.arr.join()
}
}).then(res => {
console.log(res);

if(res.success){
this.$vux.alert.show({title:'增加成功'});
setTimeout(() => {
this.$vux.alert.hide();
this.$router.push({
path: '/points'
})

}, 1000)
}else{
this.$vux.alert.show({title:res.msg});
setTimeout(() => {
this.$vux.alert.hide();
}, 1000)
}
}).catch(err => {

if(err.success){
this.$vux.alert.show({title:'增加失败'});
setTimeout(() => {
this.$vux.alert.hide();
}, 1000)
}

});}else {
alert("开始日期不能大于结束日期");
}
},

getActivity(){
},
log (str1, str2 = '') {
console.log(str1, str2)
},
showPlugin () {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm (val) {
console.log('plugin confirm', val)
},
onShow () {
console.log('plugin show')
},
onHide () {
console.log('plugin hide')
}
})
},
toggleFormat () {
this.format = this.format === 'YYYY-MM-DD HH:mm' ? 'YYYY-MM-DD' : 'YYYY-MM-DD HH:mm'
},
changeStart (value) {
this.startTime = value;
},
changeEnd (value) {
this.endTime = value;
},
clearValue (value) {
this.value6 = ''
},
clearValue8 (value) {
this.value8 = ''
},
setToday (value) {
let now = new Date();
let cmonth = now.getMonth() + 1;
let day = now.getDate();
if (cmonth < 10) cmonth = '0' + cmonth;
if (day < 10) day = '0' + day;
this.value7 = now.getFullYear() + '-' + cmonth + '-' + day;
console.log('set today ok')
},

chooseImage(it) {
wx.chooseImage({
count: 1, // 默认9
sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: res => {
let localIds = res.localIds || [];
new Promise(resolve => {
let serverIds = [];
let toUpload = localId =>
wx.uploadImage({
localId: localId, // 需要上传的图片的本地ID，由chooseImage接口获得
isShowProgressTips: 1, // 默认为1，显示进度提示
success: res => {
serverIds.push(res.serverId);
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}
})
;
if (localIds.length) {
toUpload(localIds.shift());
} else {
resolve(serverIds);
}
}).
then(serverIds => {
let promiseList = [];
serverIds.map(serverId =>
promiseList.push(
this.$http.get('picture/upload', {
params: {
mediaId: serverId
}
})
)
)
;
Promise.all(promiseList).then(result => {
let pictureIds = [];
result.map(item => pictureIds.push(item.data)
)
;
it.list.push("http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=" + pictureIds.join());
it.arr.push(pictureIds.join());
})
;
})
;
}
})
;
}
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
this.getActivity();
this.getList();
this.getUser();
}
};



